class Api {
  constructor({ baseUrl, headers }) {
    this._baseUrl = baseUrl;
    this._headers = headers;
    this._currentUser = this.getUserInfo();
  }

  _handleResponse(res) {
    if (res.ok) {
      return res.json();
    } else {
      return Promise.reject(console.log(res.status));
    }
  }

  getInitialCards() {
    return fetch(`${this._baseUrl}/cards`, {
      headers: this._headers,
    })
      .then((res) => this._handleResponse(res))
      .then((res) => {
        return res;
      })
      .catch((error) => {
        console.log(`Error, ${error}`);
      });
  }

  uploadCard(title, link, submitButton) {
    submitButton.innerText = "Saving...";

    return fetch(`${this._baseUrl}/cards`, {
      method: "POST",
      headers: this._headers,
      body: JSON.stringify({
        name: title,
        link: link,
      }),
    })
      .then((res) => this._handleResponse(res))
      .then((cardObject) => {
        submitButton.innerText = "Save";
        return cardObject;
      })
      .catch((error) => {
        console.log(`Error, ${error}`);
      });
  }

  uploadProfileImg(link, submitButton) {
    submitButton.innerText = "Saving...";

    return fetch(`${this._baseUrl}/users/me/avatar`, {
      method: "PATCH",
      headers: this._headers,
      body: JSON.stringify({
        avatar: link,
      }),
    })
      .then((res) => {
        submitButton.innerText = "Save";
        return res.json();
      })
      .catch((error) => {
        console.log(`Error, ${error}`);
      });
  }

  getUserInfo() {
    return fetch(`${this._baseUrl}/users/me`, {
      headers: this._headers,
    })
      .then((res) => this._handleResponse(res))
      .then((res) => {
        return res;
      })
      .catch((error) => {
        console.log(`Error, ${error}`);
      });
  }

  getUserImg() {
    return fetch(`${this._baseUrl}/users/me`, {
      headers: this._headers,
    })
      .then((res) => this._handleResponse(res))
      .catch((error) => {
        console.log(`Error, ${error}`);
      });
  }

  uploadUserInfo({ name, job }, submitButton) {
    submitButton.innerText = "Saving...";

    return fetch(`${this._baseUrl}/users/me`, {
      method: "PATCH",
      headers: this._headers,
      body: JSON.stringify({
        name: name,
        about: job,
      }),
    })
      .then((res) => this._handleResponse(res))
      .then((res) => {
        submitButton.innerText = "Save";
        return res;
      })
      .catch((error) => {
        console.log(`Error, ${error}`);
      });
  }

  deleteCard(cardId, deleteButton) {
    deleteButton.innerText = "Deleting...";

    return fetch(`${this._baseUrl}/cards/${cardId}`, {
      method: "DELETE",
      headers: this._headers,
    })
      .then((res) => this._handleResponse(res))
      .then((res) => {
        deleteButton.innerText = "Yes";
        return res;
      })
      .catch((error) => {
        console.log(`Error, ${error}`);
      });
  }

  removeLike(cardId) {
    return fetch(`${this._baseUrl}/cards/likes/${cardId}`, {
      method: "DELETE",
      headers: this._headers,
    })
      .then((res) => this._handleResponse(res))
      .then((res) => {
        return res;
      })
      .catch((error) => {
        console.log(`Error, ${error}`);
      });
  }

  addLike(cardId) {
    return fetch(`${this._baseUrl}/cards/likes/${cardId}`, {
      method: "PUT",
      headers: this._headers,
    })
      .then((res) => this._handleResponse(res))
      .then((res) => {
        return res;
      })
      .catch((error) => {
        console.log(`Error, ${error}`);
      });
  }
}

export const api = new Api({
  baseUrl: "https://around.nomoreparties.co/v1/group-12",
  headers: {
    authorization: "bd66ac46-8c3d-415b-b4fe-d77bd0af375a",
    "Content-Type": "application/json",
  },
});
